import React from 'react';
import { Link, withRouter } from 'react-router-dom'
import styled from 'styled-components'; /* styled-components 모듈 자바스크립트에서 css를 사용하게 해준다 */

/* 자바스크립트에서 css사용하는법*/
const Header = styled.header`
color:white;
position:fixed;
top:0px;
left:0px;
width:100%;
height:50px;
display: flex;
align-items: center;
background-color: rgba(0,0,0,0.8);
z-index:10;
box-shadow:  0px 1px 5px 2px rgba(0,0,0,0.8);
font-size:12px;
`;

const Ul = styled.ul`
display:flex;
`;

const Li = styled.li`
width:100px;
height: 50px;
text-align:center;
border-bottom:5px solid 
${props => props.current ? '#3498db' : 'transparent'};
transition:border-bottom 0.5s ease-in-out;
`;


const A = styled(Link)`
height: 50px;
display: flex;
align-items: center;
justify-content: center;
`;/* 링크 변화점은 href대신 to를 사용하며 style(Link)를 사용한다 */

export default withRouter(({ location: { pathname } }) => (
    <Header>
        <Ul>
            <Li current={pathname === '/react-flix'}>
                <A to='/react-flix'>Movies</A>
            </Li>
            <Li current={pathname === '/react-flix/tv'}>
                <A to='/react-flix/tv'>TV</A>
            </Li>
            <Li current={pathname === '/react-flix/search'}>
                <A to='/react-flix/search'>Search</A>
            </Li>
        </Ul>
    </Header>
))