import React from "react";
import styled from "styled-components";
import FlixStyle from './GlobalStyles';

const Container = styled.div`
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  font-size: 28px;
  margin-top: 20px;
`;

export default () =>
    <Container>
        <FlixStyle />
        <span role='img' aria-label='Loading'>
            Now Loading...
        </span>
    </Container>

