import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Section from '../../Components/Section';
import Loader from '../../Components/Loader';
import Message from '../../Components/Message';
import Poster from '../../Components/Poster';
import Helmet from 'react-helmet'
import Header from '../../Components/Header';
import FlixStyle from '../../Components/GlobalStyles';

const Container = styled.div`
    padding: 20px;
    margin-top:50px;
`;


const HomePresenter = ({ nowPlaying, popular, upcoming, loading, error }) =>
    loading ? (
        <Loader />
    ) : (
        <Container>
            <>
                <Helmet>
                    <title>Movies | Reactflix</title>
                </Helmet>
                <Header />
                <FlixStyle />
            </>
            <>
                {nowPlaying && nowPlaying.length > 0 && (
                    <Section title="Now Playing">
                        {nowPlaying.map(movie => (
                            <Poster
                                key={movie.id}/* 키값 */
                                id={movie.id}/* 아이디값  */
                                imageUrl={movie.poster_path}/* 이미지 경로  */
                                title={movie.title}/* 이름 */
                                rating={movie.vote_average}/* 점수 평점 */
                                year={movie.release_date && movie.release_date.substring(0, 4)} /* some 값에서 년만 ㅈ랄가져옴 */
                                isMovie={true}
                            />
                        ))}
                    </Section>
                )}
                {upcoming && upcoming.length > 0 && (
                    <Section title="Upcoming Movies">
                        {upcoming.map(movie => (
                            <Poster
                                key={movie.id}
                                id={movie.id}
                                imageUrl={movie.poster_path}
                                title={movie.title}
                                rating={movie.vote_average}
                                year={movie.release_date && movie.release_date.substring(0, 4)} /* some 값에서 년만 ㅈ랄가져옴 */
                                isMovie={true}
                            />
                        ))}
                    </Section>
                )}
                {popular && popular.length > 0 && (
                    <Section title="Popular Movies">
                        {popular.map(movie => (
                            <Poster
                                key={movie.id}
                                id={movie.id}
                                imageUrl={movie.poster_path}
                                title={movie.title}
                                rating={movie.vote_average}
                                year={movie.release_date && movie.release_date.substring(0, 4)} /* some 값에서 년만 ㅈ랄가져옴 */
                                isMovie={true}
                            />
                        ))}
                    </Section>
                )}
                {error && <Message color='#e74c3c' text={error} />}
            </>
        </Container>
    );

HomePresenter.propTypes = {
    nowPlaying: PropTypes.array,
    popular: PropTypes.array,
    upcoming: PropTypes.array,
    loading: PropTypes.bool.isRequired,
    error: PropTypes.string
};

export default HomePresenter;


